import "../styles/Contact.css"
import LeftPicture from '../assets/contact.jpg'
import CustomMotion from "../components/CustomMotion"

export default function Contact(props) {
    return (
        <CustomMotion className='contact'>

            <div className="contact-left"
                style={{ backgroundImage: `url(${LeftPicture})` }}
            ></div>
            <div className="contact-right">

                <h2>Kapcsolatfelvtel</h2>
                <form action="" method="post">
                    <label>Teljes nv</label>
                    <input type="text" placeholder="Adja meg a nevt" autoComplete="off" />

                    <label>Email</label>
                    <input type="email" placeholder="Adja meg az email cmt" autoComplete="off" />

                    <label>Telefonszm</label>
                    <input type="tel" id="phone" name="phone" pattern="[0-9]{3}-[0-9]{2}-[0-9]{3}" placeholder="Adja meg a telefonszmt" autoComplete="off" />

                    <label>Miben segthetnk?</label>
                    <textarea rows={6} placeholder="zenet..."></textarea>

                    <div className="checkbox">
                        <input type="checkbox" className="checkbox-left" />
                        <div className="checkbox-right">Elolvastam s megrtettem az NRG Solutions <a href='/adatkezelesi_tajekoztato.pdf' target="_blank">Adatkezelsi Tjkoztatjt s Szablyzatt</a>, s hozzjrulok ahhoz, hogy a megadott adataimat a szolgltat, mint adatkezel a Szablyzatban foglaltaknak megfelelen kezelje.</div>
                    </div>
                    <div className="buttons">
                        <button>Klds</button><a href="tel:06702067961">Hvjon most</a>
                    </div>
                </form>
            </div>
        </CustomMotion>
    )
}